import React, { Component } from 'react';
class Goal3 extends Component {

	render() {
		return (
			<div className="homepage--row coming-soon">
				<div className="homepage--row__content">
					<h1 className="subtitle" data-z="3">Goal #3</h1>
					<h2 className="title" data-z="4">A platform to submit your new plugins</h2>
					<div className="bodytext" data-z="2">
						<p>
							Test your plugins with the most awesome beta community. Just fill in the form, upload the necessary assets and hit publish! We'll take care of promotion 
						</p>
					</div>
					<button href="#" className="coming-soon">coming soon</button>
				</div>
			</div>
		);
	}

}


export default Goal3;



