import React, { Component } from 'react';
import * as firebase from 'firebase';
import C from '../constants';
import { auth, database } from '../firebaseApp';
import ArticleCard from './ArticleCard';
import Auth from './Auth';

class LikeButton extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isHovered: false,
			count: 0,
			isClicked: false,
			className: 'button--like'
		};
		this.handleOnClick = this.handleOnClick.bind(this);
		this.checkState = this.checkState.bind(this);
	}

	componentDidMount() {
		this.checkState();
	}

	componentDidUpdate() {
	}

	checkState() {
		if (auth.currentUser === null) {
			console.log('Oops, you need to be logged in');
		} else {
			const parent = this._reactInternalInstance._currentElement._owner._instance;
			const parentArticleID = parent.props.qid;
			const thisUserID = auth.currentUser.uid;
			const self = this;
			const checkState = firebase.database().ref('/articles/' + parentArticleID + '/likes/').once('value').then(function takeSnapshot(snapshot) {
				const likes = snapshot.val();
				if (likes) {
					if (likes.hasOwnProperty(auth.currentUser.uid)) {
						console.log(likes.hasOwnProperty(auth.currentUser.uid));
						self.setState({
							className: 'button--like button--like__active',
							isClicked: true
						});
					}
				}
			});
		}
	}

	handleOnClick(e) {
		e.preventDefault();
		if (auth.currentUser === null) {
			window.alert('If you want to  the plugins you need to login first');
		} else {
			this.setState({
				count: this.state.count + (this.state.isClicked ? -1 : 1),
				isClicked: !this.state.isClicked
			});
			if (!this.state.isClicked) {
				// ADDS LIKES IN DATABASES
				this.setState({ className: 'button--like button--like__active' });
				const parent = this._reactInternalInstance._currentElement._owner._instance;
				// add in seprated database of likes with relation of userID: postID
				// const addLikeInLikesDB = firebase.database().ref('likes/').set({
				// 	[auth.currentUser.uid]: parent.props.qid
				// });
				// added in the users database as another property of the user
				const addLikeInUserProfile = firebase.database().ref('users/' + auth.currentUser.uid + '/likes/').update({
					[parent.props.qid]: true
				});
				// added in the articles database, as a counter in the articles
				const addULikeInArticle = firebase.database().ref('articles/' + parent.props.qid + '/likes/').update({
					[auth.currentUser.uid]: true
				});
			} else {
				// REMOVES LIKES FROM DATABASES
				this.setState({ className: 'button--like' });
				const parent = this._reactInternalInstance._currentElement._owner._instance;
				const addLikeInUserProfile = firebase.database().ref('users/' + auth.currentUser.uid + '/likes/').update({
					[parent.props.qid]: null
				});
				const addULikeInArticle = firebase.database().ref('articles/' + parent.props.qid + '/likes/').update({
					[auth.currentUser.uid]: null
				});
			}
		}
	}

	render() {
		return (
			<div className="button--like__wrapper">
				<div className={this.state.className} onClick={this.handleOnClick}>
					<svg width="23" height="21" viewBox="14 14 23 21" xmlns="http://www.w3.org/2000/svg">
						<path d="M26.12 14.526l3.092 5.742c.022.042.065.075.094.082l6.367 1.266c.83.165 1.093 1.22.44 1.755l-4.533 3.706c-.05.04-.074.11-.057.188l1.285 6.44c.162.816-.684 1.462-1.428 1.09l-6.065-3.033c-.047-.023-.104-.023-.16.006l-6.056 3.028c-.745.372-1.59-.274-1.428-1.09l1.287-6.453c.013-.065-.01-.133-.06-.173l-4.532-3.706c-.655-.534-.39-1.59.438-1.754l6.34-1.26c.056-.013.1-.046.13-.106l3.083-5.724c.377-.7 1.382-.7 1.76 0z" fill="#dfdfdf" fillRule="evenodd" />
					</svg>
				</div>
				{/* {this.state.count} */}
			</div>
		);
	}
}

export default LikeButton;



