/* eslint no-new-func: 0 */
/* eslint max-len: 0 */
/* eslint no-unused-vars: 0 */
import React, { Component } from 'react';
import PluginForm from './PluginForm';

class SubmitOverlay extends Component {

	constructor(props) {
		super(props);
		this.state = {
			isActive: false
		};
		this.closeOverlay = this.closeOverlay.bind(this);
	}

	componentDidMount() {
		const jsCode = `
		(function defer() {
				 if (window.jQuery) {
							$('.control-submit-trigger').click( function(){
								$('.control-submit-trigger').addClass('submit-active');
								setTimeout(function(){
									$('body').addClass('submit-active');
								},2000)
							});

							$('.icon-close').click( function(){
								$('body').removeClass('submit-active');
								$('.control-submit-trigger').removeClass('submit-active');
							});

				 } else {
							setTimeout(function() { defer() }, 50);
				 }
		})();
		`;
		new Function(jsCode)();
	}

	closeOverlay() {
	}

	render() {
		return (
			<div className="submit--wrapper">
				<div className="control control-submit-overlay">
					<div className="btn-material">
					</div>
				</div>
				<i onClick={this.closeOverlay} className="icon-close material-icons">
					<svg xmlns="http://www.w3.org/2000/svg" width="31" height="31">
						<path fill="#066CFF" fillRule="evenodd" d="M25.95.992L15.503 11.438 5.053.992C2.35-1.715-1.71 2.344.994 5.052L11.443 15.5.994 25.945c-2.707 2.707 1.352 6.766 4.06 4.063l10.448-10.446L25.95 30.008c2.706 2.707 6.77-1.356 4.058-4.063L19.558 15.5 30.005 5.055c2.707-2.707-1.356-6.766-4.06-4.063h.005z" />
					</svg>
				</i>
				<div className="submit-overlay-content">
					<PluginForm />
				</div>
			</div>
		);
	}
}

export default SubmitOverlay;



