import React, { Component } from 'react';

class ViewProfile extends Component {
	constructor(props) {
		super(props);
		this.state = {
			color: '#066CFF',
			isClicked: false
		};
		this.showProfile = this.showProfile.bind(this);
	}

	showProfile(article) {
		console.log('()*:');
		const auth = this._reactInternalInstance._currentElement._owner._instance;
		const connect = auth._reactInternalInstance._currentElement._owner._instance;
		const navigation = connect._reactInternalInstance._currentElement._owner._instance;
		const parent = navigation._reactInternalInstance._currentElement._owner._instance;
		this.setState({
			isClicked: !this.state.isClicked
		});
		parent.setState({
			showHome: false,
			showSearch: false,
			showLikes: false,
			showStarred: false,
			showProfile: true
		});
		parent.forceUpdate();
		// if (!this.state.isClicked) {
		// 	this.setState({
		// 		color: '#333333'
		// 	});
		// 	parent.setState({
		// 		showHome: false,
		// 		showLikes: true,
		// 		showStarred: false,
		// 		showProfile: true
		// 	});
		// 	parent.forceUpdate();
		// } else {
		// 	this.setState({
		// 		color: '#066CFF'
		// 	});
		// 	parent.setState({
		// 		showHome: true,
		// 		showLikes: false,
		// 		showStarred: false,
		// 		showProfile: false
		// 	});
		// 	parent.forceUpdate();
		// }
	}
	render() {
		return (
			<button className="btn-submit btn-circle" onClick={this.showProfile}>
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="324 -64 50 50"><g fill="none" fillRule="evenodd" transform="translate(324 -64)"><circle cx="25" cy="25" r="25" fill="#066CFF" /><path fill="#FFF" fillRule="nonzero" d="M25 15.45c-1.577 0-2.87 1.293-2.87 2.87 0 1.578 1.293 2.87 2.87 2.87 1.577 0 2.87-1.292 2.87-2.87 0-1.577-1.293-2.87-2.87-2.87zm0-2c2.682 0 4.87 2.19 4.87 4.87 0 2.682-2.188 4.87-4.87 4.87s-4.87-2.188-4.87-4.87c0-2.68 2.188-4.87 4.87-4.87z" /><path fill="#FFF" d="M25 24.94c5.197 0 9.41 4.213 9.41 9.41 0 .276-.225.5-.5.5h-1.2c-.277 0-.5-.224-.5-.5 0-3.983-3.227-7.21-7.21-7.21s-7.21 3.227-7.21 7.21c0 .276-.223.5-.5.5h-1.2c-.275 0-.5-.224-.5-.5 0-5.195 4.214-9.41 9.41-9.41z" /></g></svg>
			</button>
		);
	}
}

export default ViewProfile;



