/* eslint max-len: 0 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { openAuth, logoutUser } from '../actions/auth';
import C from '../constants';
import ViewLikes from './ViewLikes';
import ViewProfile from './ViewProfile';

class Auth extends Component {

	constructor() {
		super();
		this.state = {
			overlayActive: ''
		};
		this.openSubmitOverlay = this.openSubmitOverlay.bind(this);
	}

	getJSX(props) {
		switch (props.auth.status) {
			case C.AUTH_LOGGED_IN: return (
				<div className="auth--wrapper auth--loggedin">
					{/* <span>Logged in as {props.auth.username}.</span> */}
					{" "}{/* <button className="btn-login" onClick={props.logoutUser}>Log out</button> */}
					<ViewLikes />
					<ViewProfile />
					{/* <button onClick={this.openSubmitOverlay} className="btn-submit control-submit-trigger">
						Submit plugin
					</button> */}
				</div>
			);
			case C.AUTH_AWAITING_RESPONSE: return (
				<div className="auth--wrapper auth--logging">
					<button className="btn-login btn-loading" disabled>authenticating...</button>
				</div>
			);
			default: return (
				<div className="auth--wrapper auth--loggedout">
					<button className="btn-login" onClick={props.openAuth}>
						<svg xmlns="http://www.w3.org/2000/svg" width="31" height="30"><path fill="#066CFF" d="M15 0C6.718 0 0 6.717 0 15c0 6.63 4.298 12.252 10.26 14.235.75.138 1.023-.326 1.023-.72 0-.358-.014-1.54-.02-2.794-4.173.91-5.054-1.765-5.054-1.765-.687-1.734-1.67-2.194-1.67-2.194-1.364-.93.103-.912.103-.912 1.506.105 2.3 1.546 2.3 1.546 1.337 2.293 3.51 1.63 4.364 1.245.138-.968.526-1.63.954-2.004-3.33-.38-6.833-1.667-6.833-7.415 0-1.64.586-2.975 1.543-4.026-.152-.38-.67-1.907.15-3.97 0 0 1.258-.404 4.125 1.537 1.196-.333 2.48-.5 3.754-.504 1.275.006 2.56.172 3.756.507 2.862-1.943 4.122-1.54 4.122-1.54.82 2.068.302 3.592.15 3.97.96 1.052 1.54 2.388 1.54 4.027 0 5.76-3.508 7.03-6.85 7.4.54.466 1.02 1.378 1.02 2.777 0 2.004-.02 3.62-.02 4.116 0 .4.27.866 1.03.72C25.71 27.246 30 21.623 30 15c0-8.284-6.716-15-15-15z" /></svg>
						Github login
					</button>
				</div>
			);
		}
	}

	openSubmitOverlay() {
		console.log(this);
		document.body.classList.add('submit-active');
	}

	render() {
		return this.getJSX(this.props);
	}
}

const mapStateToProps = (state) => {
	return { auth: state.auth };
};

const mapDispatchToProps = {
	openAuth,
	logoutUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);



