/* eslint no-new-func: 0 */
/* eslint max-len: 0 */
/* eslint no-unused-vars: 0 */
/* eslint no-var: 0 */
/* eslint vars-on-top: 0 */
/* eslint prefer-template: 0 */
/* eslint react/prefer-stateless-function: 0 */
/* eslint func-names: 0 */
/* eslint prefer-arrow-callback: 0 */
/* eslint no-undef: 0 */

import React, { Component } from 'react';
import Feedback from './Feedback';
import Navigation from './Navigation';
import SectionHome from './SectionHome';
import SectionSearch from './SectionSearch';
import SectionLikes from './SectionLikes';
import SectionStarred from './SectionStarred';
import SectionProfile from './SectionProfile';
import SubmitOverlay from './SubmitOverlay';
import SubmitPluginForm from './SubmitPluginForm';
import Footer from './Footer';

var ReactGA = require('react-ga');
ReactGA.initialize('UA-93507699-1');

class Page extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showHome: true,
			showSearch: false,
			showLikes: false,
			showStarred: false,
			showProfile: false
		};
	}

	render() {
		return (
			<div className="wrapper">
				<SubmitOverlay />
				<Navigation />
				<Feedback />
				{this.state.showHome ? <SectionHome /> : null}
				{this.state.showSearch ? <SectionSearch /> : null}
				{this.state.showLikes ? <SectionLikes /> : null}
				{this.state.showStarred ? <SectionStarred /> : null}
				{this.state.showProfile ? <SectionProfile /> : null}
				<Footer />
			</div>
		);
	}
}

export default Page;



