/* eslint max-len: 0 */
/* eslint no-script-url: 0 */
/* eslint vars-on-top: 0 */
/* eslint no-var: 0 */
/* eslint func-names: 0 */
/* eslint prefer-arrow-callback: 0 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
	submitArticle,
	startArticleEdit,
	cancelArticleEdit,
	submitArticleEdit,
	deleteArticle
} from '../actions/articles';
import ArticleCard from './ArticleCard';
import { auth, database, storage } from '../firebaseApp';
import fuzzyFilterFactory from 'react-fuzzy-filter';
import * as firebase from 'firebase';

const { InputFilter, FilterResults } = fuzzyFilterFactory();

class ListOfLikes extends Component {
	constructor() {
		super();
		this.submitNewArticle = this.submitNewArticle.bind(this);
		this.renderItem = this.renderItem.bind(this);
		this.state = {
			placeholder: 'Your favourites'
		};
	}

	submitNewArticle(e) {
		if (!this.props.articles.submitting) {
			e.preventDefault();
			if (this.refs.newArticle.value) {
				this.props.submitArticle(
					this.refs.newArticle.value,
					this.refs.newVersion.value,
					this.refs.newDescription.value,
					this.refs.newHomepage.value,
					this.refs.newDownload.value,
					this.refs.imageKey.value
				);
			}
			// this.refs.newArticle.value = '';
			// this.refs.newVersion.value = '';
		}
	}

	renderItem(article, index) {
		return <div key={article.key}>{article}</div>;
	}

	render() {
		const fuseConfig = {
			keys: [{
				name: 'props.content',
				weight: 0.7
			}, {
				name: 'props.description',
				weight: 0.15
			}, {
				name: 'props.author',
				weight: 0.15
			}],
			minMatchCharLength: 2
		};
		let rows = [];
		if (this.props.articles.data) {
			rows = Object.keys(this.props.articles.data).map((qid) => {
				const article = this.props.articles.data[qid];
				const imageKey = this.props.articles.data[qid].imageKey;
				const content = this.props.articles.data[qid].content;
				const description = this.props.articles.data[qid].description;
				const author = this.props.articles.data[qid].author;
				const status = this.props.articles.status[qid];
				/* eslint-disable react/jsx-no-bind */
				const thisStartArticleEdit = this.props.startArticleEdit.bind(this, qid);
				const thisCancelArticleEdit = this.props.cancelArticleEdit.bind(this, qid);
				const thisSubmitArticleEdit = this.props.submitArticleEdit.bind(this, qid);
				const thisDeleteArticle = this.props.deleteArticle.bind(this, qid);
				/* eslint-enable react/jsx-no-bind */

				const asd = this.props.articles.data[qid];
				const likes = this.props.articles.data[qid].likes;
				const uid = auth.currentUser.uid;
				const likedPlugins = '';
				console.log('-----');
				console.log(asd);
				console.log('-----');
				console.log(likes);
				console.log('-----');
				console.log(uid);
				if (likes) {
					console.log('this article haz likez');
					if (likes.hasOwnProperty(uid) && this.props.articles.data[qid].featured) {
						return (
							<ArticleCard
								key={qid}
								qid={qid}
								content={content}
								description={description}
								author={author}
								imageKey={imageKey}
								article={article}
								status={status}
								canEdit={this.props.auth.uid === article.uid}
								startArticleEdit={thisStartArticleEdit}
								cancelArticleEdit={thisCancelArticleEdit}
								submitArticleEdit={thisSubmitArticleEdit}
								deleteArticle={thisDeleteArticle}
							/>
						);
					}
				}
				return ArticleCard;
			});
		}
		let content;
		/* this.props.articles.hasReceivedData ? rows : 'Loading articles...' */
		const rowsOrLoading = this.props.articles.hasReceivedData
			? rows
			: 'Loading articles...';
		return (
			<div className="cards--wrapper">
				<div className="cards--search">
					<InputFilter
						ref="test"
						inputProps={this.state}
						className="cards--serch__input"
						debounceTime={100}
					/>
					<FilterResults
						items={rows}
						renderItem={this.renderItem}
						fuseConfig={fuseConfig}
					/>
				</div>
				{this.props.articles.errorMessage
					? <p>{this.props.articles.errorMessage}</p>
					: <div></div>
				}
				{/* <section className="cards--headline">
					<h2><span>The list has just started, if you want to add a plugin use the </span><a href="javascript:void(0)" className="control-submit-trigger">submit form</a>
					</h2>
				</section> */}
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		articles: state.articles,
		auth: state.auth
	};
};

const mapDispatchToProps = {
	submitArticle,
	startArticleEdit,
	cancelArticleEdit,
	submitArticleEdit,
	deleteArticle,
};

export default connect(mapStateToProps, mapDispatchToProps)(ListOfLikes);



