/* eslint no-new-func: 0 */
/* eslint no-unused-vars: 0 */
/* eslint max-len: 0 */
/* eslint react/prefer-stateless-function: 0 */
/* eslint prefer-template: 0 */
/* eslint no-useless-escape: 0 */

import React, { Component } from 'react';
import * as firebase from 'firebase';
import C from '../constants';
import { auth, database, storage, storageRef } from '../firebaseApp';
import ContactForm from './ContactForm';

class Footer extends Component {

	constructor() {
		super();
		this.submit = this.submit.bind(this);
		this.validateEmail = this.validateEmail.bind(this);
	}

	validateEmail(email) {
		const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return re.test(email);
	}

	submit(e) {
		e.preventDefault();
		if (this.validateEmail(this.refs.email.value)) {
			const emailData = this.refs.email.value;
			const emailString = emailData.replace(/\./g, '_');
			const createNewLead = firebase.database().ref('email-leads/' + emailString).set({
				email: this.refs.email.value,
				date: firebase.database.ServerValue.TIMESTAMP,
				trigger: 'Fixed footer campaign - "email me when ready"'
			});
			this.refs.email.value = '';
			window.alert('Thank you for subscribing ');
		}	else {
			// console.log('nope');
			// console.log(this.refs.email.className);
			// const input = this.refs.email;
			// input.className = 'shake-shake-shake';
			// console.log(this.refs.email.className);
			window.alert('Please enter a valid email address ');
		}
	}

	render() {
		return (
			<div className="footer--wrapper">
				{/* <ContactForm /> */}
				<input type="checkbox" />
				<div className="footer--content--wrapper">
					<div className="footer--content">
						<form onSubmit={this.submit}>
							<input className="" ref="email" type="email" placeholder="Email me when it's ready" required />
							<button type="submit" onClick={this.submit}>subscribe</button>
						</form>
					</div>
				</div>
			</div>
		);
	}

}


export default Footer;



