/* eslint no-new-func: 0 */
/* eslint no-unused-vars: 0 */
/* eslint max-len: 0 */
/* eslint react/prefer-stateless-function: 0 */

import React, { Component } from 'react';
class Goal1 extends Component {

	constructor(props) {
		super(props);
		this.state = {
			isClicked: false
		};
		this.showSearch = this.showSearch.bind(this);
	}

	showSearch(article) {
		console.log('()*:');
		console.log(this);
		const navigation = this._reactInternalInstance._currentElement._owner._instance;
		const parent = navigation._reactInternalInstance._currentElement._owner._instance;
		this.setState({
			isClicked: !this.state.isClicked
		});
		parent.setState({
			showHome: false,
			showSearch: true,
			showLikes: false,
			showStarred: false,
			showProfile: false
		});
		parent.forceUpdate();
	}

	render() {
		return (
			<div className="homepage--row">
				<div className="homepage--row__content">
					<h1 className="subtitle" data-z="3">Goal #1</h1>
					<h2 className="title" data-z="4">An awesome list of Sketch plugins</h2>
					<div className="bodytext" data-z="2">
						<p>Were still figuring out how to categorise the huge amount of plugins out there  By now you can search through the list and star your favourite ones</p>
					</div>
					<button onClick={this.showSearch}>view list </button>
				</div>
			</div>
		);
	}

}


export default Goal1;



