/* eslint no-new-func: 0 */
/* eslint max-len: 0 */
/* eslint no-unused-vars: 0 */
/* eslint no-var: 0 */
/* eslint vars-on-top: 0 */
/* eslint prefer-template: 0 */
/* eslint react/prefer-stateless-function: 0 */
/* eslint func-names: 0 */
/* eslint prefer-arrow-callback: 0 */

import React, { Component } from 'react';
import ReactDom from 'react-dom';
var ReactGA = require('react-ga');

class ContactForm extends Component {

	componentDidMount() {
		ReactGA.pageview('contact-form');
	}

	render() {
		return (
			<span className="contact-form">
				<span>Do you want to help building the platform?</span>
				<a href="#modal-one" className="btn openbtn">JOIN THE TEAM</a>
				<div className="modal" id="modal-one">
					<div className="modal-buttons">
						<a href="#close" className="btn closebtn">+</a>
					</div>
					<div className="modal-content">
						<p>Wow such modal! Very code</p>
					</div>
				</div>
			</span>
		);
	}
}

export default ContactForm;



