import * as firebase from 'firebase';
import C from '../constants';
import { auth, database } from '../firebaseApp';

import { listenToArticles } from './articles';


export const listenToAuth = () => {
	return (dispatch, getState) => {
		auth.onAuthStateChanged((authData) => {
			if (authData) {
				// every time that authStateChanged a new entry is created/updated (probably overkill)
				const username = 'https://api.github.com/user/' + authData.providerData[0].uid + '?access_token=f94ec355ccb1f84e6a6aa6ed636b8b69184eaf41';
				fetch(username).then(blob => blob.json()).then(response => {
					const githubname = response.login;
					const userData = firebase.database().ref('users/').child(authData.uid).update({
						username: response.login,
						email: authData.providerData[0].email,
						avatar: authData.providerData[0].photoURL,
						providerID: authData.providerData[0].providerId,
						githubID: authData.providerData[0].uid,
						date: firebase.database.ServerValue.TIMESTAMP
					});
					dispatch({
						type: C.AUTH_LOGIN,
						uid: authData.uid,
						username: response.login,
						avatar: authData.providerData[0].photoURL,
						email: authData.providerData[0].email
					});
				});

				// reload articles on auth update.
				const listenToArticlesDispatcher = listenToArticles();
				listenToArticlesDispatcher(dispatch, getState);
			} else {
				if (getState().auth.status !== C.AUTH_ANONYMOUS) {
					dispatch({ type: C.AUTH_LOGOUT });
				}
			}
		});
	};
};

export const openAuth = () => {
	return (dispatch) => {
		dispatch({ type: C.AUTH_OPEN });
		const provider = new firebase.auth.GithubAuthProvider();
		auth.signInWithPopup(provider)
			.catch((error) => {
				dispatch({
					type: C.FEEDBACK_DISPLAY_ERROR,
					error: `Login failed! ${error}`
				});
				dispatch({ type: C.AUTH_LOGOUT });
			});
	};
};

export const logoutUser = () => {
	return (dispatch) => {
		dispatch({ type: C.AUTH_LOGOUT });
		auth.signOut();
	};
};



