import React, { Component } from 'react';
import firebase from 'firebase';
import { connect } from 'react-redux';
import {
	submitArticle,
	startArticleEdit,
	cancelArticleEdit,
	submitArticleEdit,
	deleteArticle
} from '../actions/articles';
import FileUploader from 'react-firebase-image-uploader';
let customRandomID = 0;


class SubmitPluginForm extends Component {
	constructor() {
		super();
		this.submitNewArticle = this.submitNewArticle.bind(this);
	}
  componentDidMount() {
  }
	componentDidUpdate() {
	}
  handleChangeImage(e) {
    const image = e.target.files[0];
    if (image) {
      this.setState({ image });
    }
  }

  handleUploadSuccess(filename) {
		customRandomID = (Math.random() + ' ').substring(2, 10) + (Math.random() + ' ').substring(2, 10);
		console.log('handleUploadSuccess  ' + customRandomID);
    const imageKey = firebase.database().ref('images/' + customRandomID + '/').push({ filename }).key;
    console.log(imageKey, filename, customRandomID);
		document.getElementById('hiddenInputHelper').value = customRandomID;
  }

  handleImageSubmit(e) {
    e.preventDefault();
    if (this.uploader && this.state.image) {
      this.uploader.startUpload(this.state.image);
    }
	}
	submitNewArticle(e) {
		if (!this.props.articles.submitting) {
			e.preventDefault();
			if (this.refs.newArticle.value) {
				this.props.submitArticle(
					this.refs.newArticle.value,
					this.refs.newVersion.value,
					this.refs.newDescription.value,
					this.refs.newHomepage.value,
					this.refs.newDownload.value,
					this.refs.newImageKey.value
				);
			}
			// this.refs.newArticle.value = '';
			// this.refs.newVersion.value = '';
		}
	}
	render() {
		let content;
		if (this.props.auth.uid) {
			content = (
				<div className="submitplugin--form">
					<form className="the-form" id="submitplugin" onSubmit={this.submitNewArticle}>
						<ul className="grid">
              <div className="image-picker flex-column">
                  <label>Image:</label>
                  <FileUploader
                    id="test"
                    ref={c => { this.uploader = c; }}
                    accept="image/*"
                    randomizeFilename
                    storageRef={firebase.storage().ref('images')}
                    onChange={this.handleChangeImage}
                    onUploadSuccess={this.handleUploadSuccess}
                  />
              </div>
							<li style={{ display: 'none' }} className="grid__segment w--80">
									<input id="hiddenInputHelper" ref="newImageKey" className="form__input t--input input__name" type="text" placeholder="..." autoComplete="off" />
							</li>
							<div>
								<li className="grid__segment w--80">
									<div className="form__item">
										<input ref="newArticle" className="form__input t--input input__name" id="input1" type="text" placeholder="..." autoComplete="off" required="required" />
										<label className="form__label t--label" htmlFor="input1">plugin name</label>
									</div>
								</li>
								<li className="grid__segment w--20">
									<div className="form__item">
										<input ref="newVersion" className="form__input t--input label__version" id="input4" type="text" placeholder="0.0" autoComplete="off" required="required" />
										<label className="form__label t--label input__version" htmlFor="input4">version</label>
									</div>
								</li>
							</div>
							<li className="grid__segment w--100">
								<div className="form__item">
									<input ref="newHomepage" className="form__input t--input" id="input3" type="text" placeholder="..." autoComplete="off" />
									<label className="form__label t--label" htmlFor="input3">website</label>
								</div>
							</li>
							<li className="grid__segment w--100">
								<div className="form__item">
									<input ref="newDownload" className="form__input t--input" id="input3" type="text" placeholder="..." autoComplete="off" required="required" />
									<label className="form__label t--label" htmlFor="input3">download url</label>
								</div>
							</li>
							<li className="grid__segment w--100">
								<div className="form__item">
									<textarea ref="newDescription" className="form__input t--input" id="textarea1" rows="6" placeholder="Under 140 characters for promotion purposes ;)" maxLength="140" autoComplete="off" required="required" ></textarea>
									<label className="form__label t--label" htmlFor="textarea1">Description</label>
								</div>
							</li>
						</ul>
						<button htmlFor="submitplugin" type="submit" disabled={this.props.articles.submittingNew}>
							{this.props.articles.submittingNew ? 'Submitting...' : 'Submit'}
						</button>
					</form>
				</div>
			);
		} else {
			content = <p>Log in to add a new article of your own!</p>;
		}
		return (
			<div className="list--container">
				{content}
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		articles: state.articles,
		auth: state.auth
	};
};

const mapDispatchToProps = {
	submitArticle,
	startArticleEdit,
	cancelArticleEdit,
	submitArticleEdit,
	deleteArticle,
};

export default connect(mapStateToProps, mapDispatchToProps)(SubmitPluginForm);



